import styled from "styled-components";
import React from "react";
import { LayoutGrid, LayoutItemWrapper, LayoutRow } from "../layout/layout";
import { Heading, Text } from "../base/typography";

import BaseLink from "../base/links/base-link";
import TextLink from "../base/links/text-link";
import ButtonLink from "../base/links/button-link";
import MenuLink from "../base/links/menu-link";

const ColorPalette = (props) => {
    const colorArray = ['--color-primary-1', '--color-primary-2', '--color-secondary-1', '--color-neutral-0', '--color-neutral-1', '--color-neutral-2','--color-neutral-3', '--color-neutral-4', '--color-neutral-5', '--color-neutral-6', '--color-neutral-7', '--color-neutral-8','--color-neutral-9'];
    return (
        <LayoutGrid>
            {colorArray.map(entry => <ColorSwatch key={entry} color={entry} colorName={entry.substring(2)} />)}       
        </LayoutGrid>
    )
};

const ColorSwatch = (props) => {
    return (
        <ColorContainer>
            <ColorContent color={props.color} />
            <ColorTitle>{props.colorName}</ColorTitle>
        </ColorContainer>
    )
};

const ColorContainer = styled.div`
    display: flex;
    flex-direction: column;
    width: 10rem;
    border: 1px solid var(--color-neutral-7);
    box-sizing: border-box;
`;

const ColorContent = styled.div`
    flex: 1 0 auto;
    padding-bottom: 100%;
    background-color: ${({color}) => `var(${color})`};
`;

const ColorTitle = styled.div`
    flex: 1;
    padding: var(--spacing-xxs);
    text-align: center;
    background-color: var(--color-neutral-0);
`;

const SpacingGrid = () => {
    const spaceArray = ['--spacing-xs', '--spacing-s', '--spacing-base', '--spacing-m', '--spacing-l', '--spacing-xl', '--spacing-xxl'];
    return (
        <div>
            {spaceArray.map(entry => {
                return (
                    <div key={entry}>
                        <SpacingContainer space={entry}></SpacingContainer>
                        <Text>{entry.substring(2)}</Text>
                    </div>
                )
                } 
            )}
        </div>
    )};

const SpacingContainer = styled.div`
    width: ${({space}) => `var(${space})`};
    height: var(--spacing-l);
    background-color: var(--color-neutral-8);
`;

const StyleGuideContent = (props) => {
    return (
    <>
    <LayoutRow>
        <LayoutItemWrapper>
            <Heading level={3}>Colors</Heading>	
            <Heading level={5}>Light</Heading>
            <ColorPalette theme='light'/>
        </LayoutItemWrapper>
    </LayoutRow>
    <LayoutRow>
        <LayoutItemWrapper>
            <Heading level={3}>Typography</Heading>
            <Heading level={4}>Headers</Heading>
            <Heading level={1}>Header1</Heading>
            <Heading level={2}>Header2</Heading>
            <Heading level={3}>header3</Heading>
            <Heading level={4}>Header4</Heading>
            <Heading level={5}>Header5</Heading>
        </LayoutItemWrapper>
    </LayoutRow>
    <LayoutRow>
        <LayoutItemWrapper>
            <Heading level={4}>Text</Heading>
                <Text>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. A lacus vestibulum sed arcu non odio euismod. Vitae congue eu consequat ac felis donec. Diam quam nulla porttitor massa.</Text>
                <Text italic>Enim facilisis gravida neque convallis. Pretium nibh ipsum consequat nisl vel pretium lectus quam id. Facilisi morbi tempus iaculis urna id.</Text>
                <Text semibold>Enim facilisis gravida neque convallis. Pretium nibh ipsum consequat nisl vel pretium lectus quam id. Facilisi morbi tempus iaculis urna id.</Text>
                <Text bold>Enim facilisis gravida neque convallis. Pretium nibh ipsum consequat nisl vel pretium lectus quam id. Facilisi morbi tempus iaculis urna id.</Text>
                <Text black>Enim facilisis gravida neque convallis. Pretium nibh ipsum consequat nisl vel pretium lectus quam id. Facilisi morbi tempus iaculis urna id.</Text>
                <Text>Dictum varius duis at consectetur lorem donec massa. Luctus accumsan tortor posuere ac ut consequat semper viverra. Est ante in nibh mauris cursus mattis.  consequat ac felis donec. Viverra mauris in aliquam sem. Pharetra vel turpis nunc eget lorem dolor. Vitae congue eu <TextLink to="#">TextLink</TextLink> Egestas erat imperdiet sed euismod nisi porta. Ipsum a arcu cursus vitae congue. Quis auctor elit sed vulputate mi. At tempor commodo ullamcorper a lacus vestibulum sed arcu. Proin fermentum leo vel orci porta non pulvinar. Amet consectetur adipiscing elit pellentesque habitant morbi tristique senectus et. </Text>
            </LayoutItemWrapper>
    </LayoutRow>
    <LayoutRow>
        <LayoutItemWrapper>
            <Heading level={3}>Spacing</Heading>
            <SpacingGrid />
        </LayoutItemWrapper>
    </LayoutRow>
    <LayoutRow>
        <LayoutItemWrapper>
            <Heading level={3}>ButtonLinks</Heading>
            <div>
                <BaseLink to="#">BaseLink</BaseLink>
            </div>
            <div>
                <MenuLink to="#">MenuLink</MenuLink>
            </div>
            <div>
                <ButtonLink to="#">ButtonLink</ButtonLink>
            </div>
        </LayoutItemWrapper>
    </LayoutRow>
    </>
    )
};

export default StyleGuideContent;